import React from 'react'
import {
    Cell,
    Group,
    List,
    Panel,
    PanelHeader,
    View,
    Button,
    Div
} from '@vkontakte/vkui'
import '@vkontakte/vkui/dist/vkui.css'
import SweetSelect from './SweetSelect'

class Home extends React.Component {
    constructor(props) {
        super(props)
        this.state = this.getInitialState()
    }

    changePanel(id) {
        this.setState({activePanel: id})
    }

    goHome() {
        this.changePanel('main')
    }

    render() {
        return (
            <View
                id={this.props.id}
                header={true}
                activePanel={this.state.activePanel}
            >
                <Panel id="main">
                    <PanelHeader>Расчет ТО</PanelHeader>
                    <Group title="Рассчитайте стоимость технического обслуживания машины:">
                        <List>
                            <Cell
                                indicator={this.state.model.text}
                                onClick={() => this.changePanel('chooseModel')}
                            >
                                Модель авто
                            </Cell>
                            <Cell
                                indicator={this.state.modification.text}
                                onClick={() =>
                                    this.changePanel('chooseModification')
                                }
                            >
                                Модификация
                            </Cell>
                            <Cell
                                indicator={this.state.oldness.text}
                                onClick={() =>
                                    this.changePanel('chooseOldness')
                                }
                            >
                                Пробег или время
                            </Cell>
                        </List>
                    </Group>
                    <Group title={'Работы'}>
                        <Cell indicator={'2000 руб'}>Покраска</Cell>
                        <Cell indicator={'2000 руб'}>Всего по работам</Cell>
                    </Group>
                    <Group title={'Запчасти'}>
                        <Cell indicator={'5000 руб'}>Краска</Cell>
                        <Cell indicator={'5000 руб'}>Всего по запчастям</Cell>
                    </Group>
                    {/*<Group title={'Итого'}>*/}
                    {/*<Cell indicator={'2000'}>Всего по работам</Cell>*/}
                    {/*<Cell indicator={'5000'}>Всего по запчастям</Cell>*/}
                    {/*</Group>*/}
                    <Group>
                        <Cell indicator={'7000 руб'}>Итого</Cell>
                    </Group>
                    <Div>
                        <Button align="center" stretched size="l">
                            Отправить заявку
                        </Button>
                    </Div>
                </Panel>
                <SweetSelect
                    id="chooseModel"
                    backClickHandler={() => this.goHome()}
                    header="Select model"
                    items={[{id: 1, value: 'panamera'}]}
                    onSelect={(item) => this.setSelectValue('model', item)}
                />
                <SweetSelect
                    id="chooseModification"
                    backClickHandler={() => this.goHome()}
                    header="Select modification"
                    items={[{id: 1, value: 'cool'}]}
                    onSelect={(item) =>
                        this.setSelectValue('modification', item)
                    }
                />
                <SweetSelect
                    id="chooseOldness"
                    backClickHandler={() => this.goHome()}
                    header="Select Oldness"
                    items={[{id: 1, value: '1k'}]}
                    onSelect={(item) => this.setSelectValue('oldness', item)}
                />
            </View>
        )
    }

    setSelectValue(key, item) {
        this.setState({
            [key]: this.convertFromSweetSelectToHomeItemsFormat(item)
        })
    }

    getInitialState() {
        const state = {}
        state.activePanel = 'main'
        Object.assign(state, this.getDefaultSelectState('Выбрать'))
        return state
    }

    convertFromSweetSelectToHomeItemsFormat(item) {
        return {
            id: item.id,
            text: item.value
        }
    }

    getDefaultSelectState(text) {
        return {
            model: {id: 0, text},
            modification: {id: 0, text},
            oldness: {id: 0, text}
        }
    }
}

export default Home



